<template>
	<div class="verbal-container">
		<!--<div class="verbal-header">
			<div class="verbal-guntu">
				<mu-carousel>
					<mu-carousel-item v-for="(item,index) in bannerlist" :key="index">
						<img :src="baseUrl+item.banner_pic">
					</mu-carousel-item>
				</mu-carousel>
			</div>
			<div style="padding-top: 2rem;">
				<div @click="go_search()">
					<div class="verbal-frame">
						<div class="verbal-picture">
							<img src="../assets/icon_search.png">
						</div>
						<div class="verbal-sousuo">
							<input type="text" placeholder="聊天套路" v-model="content">
						</div>
					</div>
					<div class="verbal-sou">搜索</div>
				</div>
			</div>
		</div>-->
		<div class="verbal-header">
			<div style="padding-top: 1rem;">
				<div>
					<div class="verbal-frame">
						<div class="verbal-picture">
							<img src="../assets/tubiao2-huiyuan.png" />
						</div>
						<div class="verbal-sousuo">
							<input type="text" placeholder="输入关键词进行搜索" v-model="content">
						</div>
					</div>
					<div class="verbal-sou" @click="go_search(content)">搜索</div>
				</div>
			</div>
		</div>
		<div class="verbal-content">
			<div class="verbal-chuqi">精彩专题</div>
			<div class="verbal-hua" :class="{'verbal-guang': index == 1}" :style="{backgroundImage:'url('+img_list[index]+')'}" v-for="(item,index) in special_list" :key="index" @click="go_discuss(item.special_id,item.special_name)">
				<!--<div class="verbal-shu" :style="'backgroundImage:url('+item.special_pic+')'">{{item.special_name}}</div>-->
			</div>
			<div style="margin-bottom: 13rem;"></div>
		</div>
		<div class="verbal-footer">
			<mu-bottom-nav>
				<mu-bottom-nav-item title="首页" icon=" " to="home" tag="span"></mu-bottom-nav-item>
				<mu-bottom-nav-item title="话术" icon=" " to="verbal" tag="span"></mu-bottom-nav-item>
				<mu-bottom-nav-item title="讨论" icon=" " to="discussion" tag="span"></mu-bottom-nav-item>
				<mu-bottom-nav-item title="我的" icon=" " to="mine" tag="span"></mu-bottom-nav-item>
			</mu-bottom-nav>
		</div>
	</div>
</template>

<script>
	import Qs from 'qs'
	export default {
		name: 'home',
		data() {
			return {
				msg: '',
				special_list: [],
				bannerlist: '',
				data: [],
				toptagid: '',
				content: '',
				data: {},
				img_list:[
					'./static/lianaihuashu.png',
					'./static/lianaiguanli.png',
					'./static/gaojiejiqiao.png'
				]
			}
		},
		methods: {
			go_search(content){
//				alert(content)
//				this.$router.push('/search')
				this.$router.push({
					path:'search',
					query:{
						type:content,
					}
				})
			},
			go_discuss(special_id,special_name){
				this.$router.push({
					path:'discuss',
					query:{
						type:special_id,
						typeone:special_name
					}
				})
			},
			go_tradition(){
				this.$router.push('/tradition')
			},
			go_acrobatics(){
				this.$router.push('/acrobatics')
			},
			specials() {
				var that = this;
				that.$http.post('/special/specials', {}).then((res) => {
					if (res.meta.code == 200) {
						that.special_list = res.data;
					}
				})
			},
			banner() {
				var that = this;
				that.$http.post('/banner/banners', {}).then((res) => {
					if(res.meta.code == 200) {
					that.bannerlist = res.data;
					}
				})
			},
			//			个人信息
			userInfo() {
				var that = this;
				var params = {
					userid: that.user_info.user_id,
//					discussionid: that.discussionid
				};
				that.$http.post('/user/userInfo', Qs.stringify(params), {

				}).then((res) => {
					if(res.meta.code == 200) {
						that.data = res.data;
					}
				})
			}
		},
		created() {
			// 页面刚加载的时候执行
			if(!!localStorage.getItem('user_info')) {
				this.user_info = JSON.parse(localStorage.getItem('user_info'));
			}
			this.banner();
			this.specials();
		},
		mounted() {
			// 页面刚加载完 dom 也加载完 的时候执行

		}
	}
</script>

<style scoped>
	.verbal-container {
		width: 100%;
		height: 100%;
		background: #FFFFFF;
	}
	
	.verbal-header {
		background-image: url('../assets/banner-huashu.png');
		background-repeat: no-repeat;
		background-size: 100% 100%;
		width: 100%;
		height: 17rem;
		margin-bottom: 1rem;
		/*background: #f1dbd9;*/
	}
	.verbal-guntu img {
		width: 100%;
		height: 200px;
	}
	.verbal-frame {
		/*border: 1px solid #ffcccc;*/
		border: 1px solid #fff;
		border-radius: 25px;
		background: #fff;
		/*background: #fff;*/
		width: 65%;
		display: flex;
		align-items: center;
		margin: 0 auto;
		margin-left: 1.5rem;
		/*margin-left: 4rem;*/
		/*position: absolute;
		top: 1rem;*/
	}
	
	.verbal-picture img {
		width: auto;
		height: 1.3rem;
		padding-left: 1rem;
		padding-right: 1rem;
		margin-top: 0.6rem;
		margin-bottom: 0.2rem;
		/*padding-top: 0.3rem;*/
	}
	
	.verbal-sousuo input {
		font-size: 1.4rem;
		height: 1.8rem;
		padding: 0.3rem 0;
		line-height: 1.8rem;
		width: 95%;
		color: #f683a2;
		border: none;
		outline: none;
		/*background: rgba(0, 0, 0, 0.01);*/
		padding-top: 0.3rem;
	}
	
	.verbal-sou {
		border: 1px solid #fff;
		border-radius: 15px;
		background: #fff;
		color: #f683a2;
		padding: 0.2rem 0.8rem;
		position: absolute;
		top: 1.2rem;
		right: 1.5rem;
		font-size: 1.3rem;
	}
	.verbal-content {
		width: 100%;
		height: calc(100% - 135px);
		overflow-x: hidden;
		overflow-y: auto;
	}
	.verbal-chuqi{
		font-size: 1.5rem;
		color: #000000;
		border-left: 2px solid #fc5d98;
		padding-left: 0.8rem;
		margin-left: 1.5rem;
		color: #fc5d98;
	}
	.verbal-hua{
		background-image: url('../assets/img_1.png');
		background-repeat: no-repeat;
		background-size: 100% 100%;
		width: 95%;
		height: 150px;
		/*margin-top: 1.5rem;*/
		display: flex;
		align-items: center;
		margin: 1.5rem auto;
	}
	.verbal-shu{
		font-size: 3rem;
		color: #FEFEFE;
		border: 1px solid #7b7777;
		background: rgba(0,0,0,0.42);
		padding: 0.1rem 0.5rem;
	}
	.verbal-guang{
		background-image: url('../assets/img_2.png');
		background-repeat: no-repeat;
		background-size: 100% 100%;
		width: 95%;
		height: 150px;
		/*margin-top: -1.5rem;*/
		display: flex;
		flex-direction: row-reverse;
		align-items: center;
	}
	.verbal-li{
		/*font-size: 3rem;
		color: #FEFEFE;
		border: 1px solid #7b7777;
		background: rgba(0,0,0,0.42);
		padding: 0.1rem 0.5rem;
		display: flex;
		flex-direction: row;*/
	}
	.verbal-jie{
		background-image: url('../assets/img_3.png');
		background-repeat: no-repeat;
		background-size: 100% 100%;
		width: 100%;
		height: 150px;
		margin-bottom: 2rem;
		margin-top: 1.5rem;
		display: flex;
		align-items: center;
	}
	.verbal-qiao{
		font-size: 3rem;
		color: #FEFEFE;
		border: 1px solid #7b7777;
		background: rgba(0,0,0,0.42);
		padding: 0.1rem 0.5rem;
		margin-top: 3rem;
	}
	.verbal-footer {
		height: 55px;
		box-sizing: border-box;
		overflow: hidden;
		position: fixed;
		bottom: 0;
		width: 100%;
		background: #fff;
		border-top: 1px solid #f0f0f0;
	}
	/deep/ .mu-carousel{
		height: 200px;
	}
	/*/deep/ .mu-carousel-button.mu-icon-button {
		top: 25%
	}*/
	/deep/ .mu-carousel-indicators{
		top: 13rem;
	}
	/deep/ .mu-tabs-inverse {
		background-color: #FFFFFF;
	}
	
	/deep/ .mu-tab-wrapper {
		padding: 0;
	}
	
	/deep/ .mu-bottom-item-icon {
		margin-top: 6px;
		background-repeat: no-repeat;
		width: 20px;
		height: 20px;
		background-size: 100% 100%;
	}
	/*/deep/ .mu-bottom-item-text {
		color: #999999
	}*/
	/*/deep/ .mu-bottom-item-active .mu-bottom-item-text {
		color: #229efe;
	}*/
	
	/deep/ .mu-bottom-item-active {
		color: rgba(0, 0, 0, .54);
	}
	
	/deep/ .mu-bottom-item:nth-child(1) .mu-bottom-item-icon {
		background-image: url(../assets/tab_shouye1.png);
	}
	
	/deep/ .mu-bottom-item:nth-child(2) .mu-bottom-item-icon {
		background-image: url(../assets/huassu2.png);
	}
	
	/deep/ .mu-bottom-item:nth-child(3) .mu-bottom-item-icon {
		background-image: url(../assets/tab_taolun1.png);
	}
	
	/deep/ .mu-bottom-item:nth-child(4) .mu-bottom-item-icon {
		background-image: url(../assets/tab_wode1.png);
	}
	
	/deep/ .mu-bottom-item:nth-child(2) .mu-bottom-item-text {
		color: #f683a2;
		font-size: 14px;
	}
	/deep/ .mu-bottom-item:nth-child(1) .mu-bottom-item-text {
		font-size: 10px;
	}
	/deep/ .mu-bottom-item {
		min-width: 0px;
	}
	/*/deep/ .mu-bottom-item-text {
		color: #000000
	}
	
	/deep/ .mu-bottom-item-active .mu-bottom-item-text {
		color: #8CADF5;
	}*/
</style>


